.ng2-c-tiles {
  color: #91b1c4;
  display: block;
  margin-left: -10px; }

.ng2-c-tile {
  box-sizing: border-box;
  float: left;
  padding-bottom: 10px;
  padding-left: 10px;
  width: 50%; }
  @media (min-width: 414px) {
    .ng2-c-tile {
      width: 33.33%; } }
  @media (min-width: 1024px) {
    .ng2-c-tile {
      width: 25%; } }
  @media (min-width: 1200px) {
    .ng2-c-tile {
      width: 20%; } }

.ng2-c-tile__container {
  background: #ffffff;
  border-radius: 2px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  display: block;
  height: 196px;
  overflow: hidden;
  position: relative;
  width: 100%; }

.ng2-c-tile__checkbox {
  position: absolute;
  right: 10px;
  top: 10px; }

.ng2-c-tile__text {
  bottom: 20px;
  color: #cccccc;
  font-size: 18px;
  left: 0;
  line-height: 23px;
  position: absolute;
  right: 0;
  text-align: center; }

.ng2-c-tile__icon {
  color: #cccccc;
  font-size: 50px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -75%); }

.ng2-c-tile__container--is-active {
  background-color: #ffffff;
  box-shadow: none; }
  .ng2-c-tile__container--is-active .ng2-c-tile__icon,
  .ng2-c-tile__container--is-active .ng2-c-tile__text {
    color: #28648a; }
